@import url(https://fonts.googleapis.com/css?family=Poppins);
@import url(https://fonts.googleapis.com/css?family=Montserrat);
@import url(https://fonts.googleapis.com/css?family=Lato);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  color: rgba(45,45,45,0.98);
  color : rgba(45,45,45,0.98);
  color : var(--color-textcolor);
  background-color:white;
  background-color: white;
  background-color: var(--color-background);
  font-family: Montserrat;
  -webkit-transition: background-color 1s ease-out,color 0.7s ease-in,box-shadow 0.5s ease-in-out;
  transition: background-color 1s ease-out,color 0.7s ease-in,box-shadow 0.5s ease-in-out;
  will-change: transition, box-shadow, background-color
}

html{
  height: 100%;
  /*added for outline none in mobile devices*/
  -webkit-tap-highlight-color: transparent;
}

*::-webkit-scrollbar {
  width: 1em;
}

*::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 0px rgba(45,45,45,0.98);
  -webkit-box-shadow: inset 0 0 0px var(--color-textcolor);
}

*::-webkit-scrollbar-thumb {
  background-color: rgba(45,45,45,0.98);
  background-color: var(--color-textcolor);
  outline: 0px solid white;
  outline: 0px solid var(--color-background);
  border-radius: 50px;
}

:root {
  --color-background:white;
  --color-textcolor: rgba(45,45,45,0.98);
  --color-theme: rgba(11, 122, 231, 1);
  --color-gradient:#73baf3;
}

body{
  height:100%;
  margin: 0px;
  padding: 0px;
}

.page-enter {
  opacity: 0;
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.page-enter-active {
  opacity: 1;
  -webkit-transform: scale(1);
          transform: scale(1);
  -webkit-transition: opacity 300ms, -webkit-transform 300ms;
  transition: opacity 300ms, -webkit-transform 300ms;
  transition: opacity 300ms, transform 300ms;
  transition: opacity 300ms, transform 300ms, -webkit-transform 300ms;
}

.page-exit {
  opacity: 1;
  -webkit-transform: scale(1);
          transform: scale(1);
}

.page-exit-active {
  opacity: 0;
  -webkit-transform: scale(0.9);
          transform: scale(0.9);
  -webkit-transition: opacity 300ms, -webkit-transform 300ms;
  transition: opacity 300ms, -webkit-transform 300ms;
  transition: opacity 300ms, transform 300ms;
  transition: opacity 300ms, transform 300ms, -webkit-transform 300ms;
}
.navbar{
    width:100%;
    background-color:white ;
    background-color: var(--color-background) !important;
    height: 50px;
    top:0;
    left:0;
}

.navbar_comp{
    height:100%;
    display: flex;
    align-items: center;
    padding-left: 10px;
}

.icon_holder{
    float: right;
    margin-left: auto;
}

.ext_icon{
    width:40px;
    height:40px;
    cursor: pointer;
}
.toggler{
    width:3%;
    height:50%;
    display: flex;
    flex-direction: column;
    border: none;
    cursor: pointer;
    background: transparent;
    justify-content: space-around;
    box-sizing: border-box;
    padding: 0px !important;
    outline: none;
    margin-left: 5px;
}

.toggler:hover .toggle_line:nth-child(1){
    -webkit-transform: skewY(-40deg);
            transform: skewY(-40deg);
    width: 60%;
}

.toggler:hover .toggle_line:nth-child(3){
    -webkit-transform: skewY(40deg);
            transform: skewY(40deg);
    width: 60%;
}

.toggle_line{
    -webkit-transition:-webkit-transform 0.5s ease-in;
    transition:-webkit-transform 0.5s ease-in;
    transition:transform 0.5s ease-in;
    transition:transform 0.5s ease-in, -webkit-transform 0.5s ease-in;
    width: 30px;
    height: 2px;
    background: rgba(11, 122, 231, 1);
    background: var(--color-theme);
}
.slider {
    height: 100%;
    background: white;
    background: var(--color-background) !important;
    position: fixed;
    top: 0;
    left: 0;
    width: 75%;
    max-width: 250px;
    display: flex;
    flex-direction: column;
    z-index: 3;
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
    -webkit-transition: all 0.3s ease-out;
    transition: all 0.3s ease-out;
}

.image_holder {
    height: 100%;
    max-height: 200px;
    display: flex;
    flex-direction: column;
    margin-bottom: 20px;
}

.profile_image {
    height: 100%;
    max-height: 200px;
    border-radius: 78%;
    margin: 10px 10% 10px 10%;
}

.nav_content {
    width: 100%;
    height: 100%;
}

.nav_button {
    width: 100%;
    height: 40px;
    background: transparent;
    border: none;
    margin-bottom: 5px;
    cursor: pointer;
    outline: none;
    text-align: left;
    padding-left: 40%;
}

.nav_button::first-letter {
    text-transform: uppercase;
}

.nav_button:hover {
    background: #73baf3;
    background: var(--color-gradient);
}

.style-ul {
    list-style-type: none;
    display: flex;
    flex-direction: column;
    padding: 0px;
}

.li-styl {
    display: flex;
    padding-left: 35%
}

.slider.open {
    left: 0;
    height: 100%;
    box-shadow: 2px 0px 5px black;
    -webkit-transform: translateX(0);
            transform: translateX(0);
}

.loadingIndicator{
    width: 25%;
    height: 25%;
    max-width: 75px;
    max-height: 75px;
    overflow: auto;
    margin: auto;
    position: absolute;
    top: 10%; left: 0; bottom: 0; right: 0;
}
.backdrop {
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: 2;
    position: fixed;
    background: -webkit-linear-gradient(45deg, #73baf3, transparent);
    background: linear-gradient(45deg, #73baf3, transparent);
    background: -webkit-linear-gradient(45deg, var(--color-gradient), transparent);
    background: linear-gradient(45deg, var(--color-gradient), transparent);
}
.container {
    margin: 2% 15% 2% 15%;
    width: 70%;
}

.cardContainer {
    box-shadow: 0 4px 4px 0 rgba(45,45,45,0.98);
    box-shadow: 0 4px 4px 0 var(--color-textcolor);
    margin-bottom: 20px;
    min-height: 100px;
    cursor: pointer;
    background-color: rgba(45,45,45,0.98);
    background-color: var(--color-textcolor);
    background: white;
    background: var(--color-background);
    padding: 10px 10px 10px 10px;
}

.cardText {
    word-wrap: break-word;
}

summary {
    color:rgba(11, 122, 231, 1);
    color: var(--color-theme)
}

.cardTextHeader {
    font-family: Poppins;
    outline: none
}

.cardTextContent {
    font-family: Lato
}

.cardContainer:hover {
    box-shadow: 2px 4px 6px rgba(11, 122, 231, 1);
    box-shadow: 2px 4px 6px var(--color-theme);
}

@media(max-width:600px) {
    .cardContainer:hover {
        pointer-events: none;
        box-shadow: 2px 4px 6px rgba(11, 122, 231, 1);
        box-shadow: 2px 4px 6px var(--color-theme);
    }
}
.container{
    margin: 2% 15% 2% 6%;
    width: 80%;
}

.border-btm{
    border-bottom:1px solid rgba(11, 122, 231, 1);
    border-bottom: 1px solid var(--color-theme);
    padding-bottom: 2%
}

.line-1_7{
    line-height: 1.7em
}
.header_resume{
    padding: 0px 0px 2% 0px;
    margin:2% 6%;
    display:table;
    display: flow-root;
    font-family: Montserrat;
    border-bottom: 1px solid rgba(11, 122, 231, 1);
    border-bottom: 1px solid var(--color-theme);
    width: 90%;
}

@media(max-width: 600px) {
    .header_resume{
        display: flex;
    }

    .sectionTitle{
        width: 100% !important;
    }

    .sectionContent{
        width:100% !important;
    }
}

.name-headr{
    color:rgba(11, 122, 231, 1);
    color: var(--color-theme);
    font-size: 2em;
    font-weight: 400;
    margin-bottom: 1%;
}

.flt-right ul{
    list-style-type: none;
    font-size: 1em;
    margin-top: 10%;
    font-weight: 600;
}

.flt-right ul li{
    margin-bottom: 10px;
}

.section-container ul{
    padding: 0px;
    list-style-type: none;
}

.section-container ul li{
    margin-bottom: 10px;
}

.flt-right{
    margin-top: 2%;
    float: right
}

.main-inline{
    display:inline-block;
}

.section-container{
    padding-bottom: 2%;
    display:inline-block;
}

.margin-section{
    margin: 0% 6%;
    display:inline-block;
}

.sectionTitle{
    color:rgba(11, 122, 231, 1);
    color: var(--color-theme);
    float: left;
    width:30%;
    font-family: Montserrat;
    text-transform: uppercase;
    word-wrap: break-word;
}

.sectionContent{
    float: right;
    font-family: Poppins;
    width:70%
}

.sectionContent p{
    line-height: 28px
}

.flt-left{
    float: left
}
.modalContent {
    position: fixed;
    z-index: 1;
    width: 300px;
    margin: auto;
    min-height: 300px;
    bottom: 1%;
    right: 1%;
    border: 1px solid #73baf3;
    border: 1px solid var(--color-gradient);
    display: flex;
    flex-direction: column;
    -webkit-transition: all 0.4s ease-in;
    transition: all 0.4s ease-in;
    height: 30%;
}

.wrapper {
    display: flex;
    flex: 1 1 auto;
    flex-direction: column;
    height: 100%;
}

.close {
    color: rgba(45, 45, 45, 0.98);
    color: var(--color-textcolor);
    float: right;
    font-size: 28px;
    font-weight: bold;
    background-color: transparent;
    cursor: pointer;
    outline: none;
    width: 30px;
}

.chat_title {
    background: rgba(11, 122, 231, 1);
    background: var(--color-theme);
    display: flex;
    flex: 1 1 5%;
    min-height: 2.2em;
    max-height: 2.2em;
    justify-content: flex-end;
}

.chatContent {
    display: flex;
    flex: 1 1 85%;
    flex-wrap: wrap;
    align-items: flex-start;
    align-content: flex-start;
    overflow: auto;
}

.chatRoom {
    width: 100%;
    display: flex;
    min-height: 2em;
    max-height: 2em;
    justify-content: space-around;
    padding-bottom: 2%;
}

.chatTextbox {
    width: 75%;
    border-radius: 10px;
    outline: none;
}

.messages {
    width: 100%;
    height: 100%;
    overflow-y: auto;
    display: flex;
    flex-direction: column;
}

.self {
    border: 2px solid #dedede;
    background-color: white;
    background-color: var(--color-background);
    border-radius: 5px;
    padding: 10px;
    margin: 10px 0;
    font-family: monsterat;
    order: 1;
    display: flex;
}

.message {
    display: flex;
    justify-content: flex-end;
    border-top-right-radius: 10px;
}

.messageTheirs {
    display: flex;
    justify-content: flex-start;
    border-top-left-radius: 10px;
}

.chat-button {
    position: fixed;
    right: 10px;
    bottom: 10px;
    z-index: 1;
    cursor: pointer;
    outline: none;
}

.chatClosed {
    position: absolute;
    font-family: lato;
    width: 100%;
    text-align: center;
    bottom: 2px;
    color: rgba(11, 122, 231, 1);
    color: var(--color-theme);
}

.chatSend {
    width: 20px;
    border-radius: 10px;
    outline: none;
    cursor: pointer;
    image-rendering: crisp-edges;
}
