.slider {
    height: 100%;
    background: white;
    background: var(--color-background) !important;
    position: fixed;
    top: 0;
    left: 0;
    width: 75%;
    max-width: 250px;
    display: flex;
    flex-direction: column;
    z-index: 3;
    transform: translateX(-100%);
    transition: all 0.3s ease-out;
}

.image_holder {
    height: 100%;
    max-height: 200px;
    display: flex;
    flex-direction: column;
    margin-bottom: 20px;
}

.profile_image {
    height: 100%;
    max-height: 200px;
    border-radius: 78%;
    margin: 10px 10% 10px 10%;
}

.nav_content {
    width: 100%;
    height: 100%;
}

.nav_button {
    width: 100%;
    height: 40px;
    background: transparent;
    border: none;
    margin-bottom: 5px;
    cursor: pointer;
    outline: none;
    text-align: left;
    padding-left: 40%;
}

.nav_button::first-letter {
    text-transform: uppercase;
}

.nav_button:hover {
    background: #73baf3;
    background: var(--color-gradient);
}

.style-ul {
    list-style-type: none;
    display: flex;
    flex-direction: column;
    padding: 0px;
}

.li-styl {
    display: flex;
    padding-left: 35%
}

.slider.open {
    left: 0;
    height: 100%;
    box-shadow: 2px 0px 5px black;
    transform: translateX(0);
}